.s-video-rooms {
	position: relative;
	padding: clamp(40px, 6vw, 92px) 0;

	.container {
		max-width: 1100px;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			height: 1px;
			background: linear-gradient(270deg, rgba(187, 255, 0, 0) 0%, #BBFF00 50%, rgba(187, 255, 0, 0) 100%);
			opacity: 0.3; } }

	.section {
		&__title {
			@include bold;
			font-size: clamp(32px, 4vw, 64px);
			margin-bottom: clamp(24px, 3vw, 38px); }

		&__subtitle {
			margin-bottom: 38px;
			line-height: 1.36;
			max-width: 880px;
			color: $gray;
			@include sm {
				margin-bottom: 32px; } } } }


.video-rooms {
	margin: 62px 0 0;
	gap: 30px;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));

	@include md {
		margin: 30px 0 0; }
	@include sm {
		margin: 10px 0 0; }

	&__item {
		--clipStep: 13px;
		position: relative;
		padding: 32px 32px 22px;
		background: #131513;
		display: flex;
		flex-direction: column;
		align-items: center;
		transition: all ease 0.1s;
		background: $themeColorD;
		clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0);

		&:after {
			content: '';
			position: absolute;
			left: 1px;
			right: 1px;
			top: 1px;
			bottom: 1px;
			background: $mainBg;
			z-index: -1;
			--clipStep: 13px;
			clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0); }

		&.hoverable {

			&:hover {

				.video-rooms__btn {
					border-color: $themeColor; } } } }

	&__link {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		font-size: 0;
		text-decoration: none;
		&:focus-visible  ~ {
			.video-rooms__btn {
				border-color: $themeColor; } } }

	&__img-wrap {
		display: flex;
		width: 100%;
		height: 120px;
		justify-content: center; }

	&__img {
		display: block;
		margin:  auto;
		max-height: 100%; }

	&__num {
		display: none; }

	&__title {
		display: none; }

	&__users {
		margin: 34px auto 26px; }

	&__user {
		display: flex;
		align-items: center;
		flex: 1 1 auto;
		margin: 20px auto 0;

		&:first-child {
			margin: 0; } }

	&__avatar {
		width: 44px;
		height: 44px;
		margin: 0 16px 0 0;
		object-fit: cover; }

	&__name {
		font-size: 12px;
		line-height: 1.28; }

	&__btn {
		width: 100%;
		font-size: 12px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid transparent;
		min-height: 48px;
		font-weight: 500;
		padding: 5px 15px;
		text-decoration: none;
		margin: auto 0 0;
		text-transform: uppercase;
		text-align: center;
		transition: all ease 0.1s; } }


a.video-rooms__btn {
	color: #bbff00;
	border-color: #bbff00;
	&:hover, &:focus-visible {
		background: #bbff00;
		color: $black; } }
