.s-dates {
	margin: clamp(60px, 8vw, 110px) 0 50px;

	.container {
		padding: 0;

		@include sm {
			padding: 0; } } }

.dates {
	display: flex;
	gap: 0 30px;
	flex-wrap: wrap;
	max-width: 1110px;
	margin: 0 auto;

	@include laptop {
		gap: 0 26px; }

	@include md {
		gap: 20px;
		max-width: 620px;
		margin: 0 auto; }

	@include xs {
		max-width: 320px; }

	&__item {
		position: relative;
		flex: 1 1;
		padding: 24px 24px 24px;
		min-width: 0;
		background: $mainBg;
		min-width: 200px;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			clip-path: polygon(calc(100% - 18px) 0, 100% 18px, 100% 100%, 18px 100%, 0 calc(100% - 18px), 0 0);
			background: $themeColor;
			z-index: 0;
			opacity: 0.4; }

		&:after {
			content: '';
			position: absolute;
			left: 1px;
			right: 1px;
			top: 1px;
			bottom: 1px;
			clip-path: polygon(calc(100% - 17px) 0, 100% 17px, 100% 100%, 17px 100%, 0 calc(100% - 17px; }
		@include sm {
			padding: 0 20px), 0 0);
			background: $black;
			z-index: 1; }

		@include xs {
			flex: 1 1 100%; } }

	&__icon {
		position: absolute;
		transform: translateY(-5px);
		top: 50%;
		right: -20px;

		@include laptop {
			right: -20px; }

		@include md {
			display: none; }

		.icon {
			fill: $themeColor;
			width: 10px;
			height: 10px; } } }

.date {
	text-decoration: none;
	height: 100%;
	position: relative;
	z-index: 2;

	&__inner {
		display: flex;
		flex-direction: column;
		flex: 1;
		height: 100%; }

	&__title {
		color: $white;
		line-height: 1.34;
		font-size: 24px;
		text-transform: uppercase;
		min-height: 120px;
		@include bold; }

	&__date {
		color: $themeColor;
		line-height: 1.28;
		text-transform: uppercase;
		margin: 0;
		@include bold;
		font-weight: 500;

		strong {
			display: block;
			font-size: 48px; } }

	&__name {
		color: $white;
		font-size: 12px;
		font-weight: 500;
		text-align: center;
		text-transform: uppercase;
		margin: 20px 0 30px; }

	&__body {
		margin: 68px 0 0; }

	&__info {
		color: $gray;
		font-size: 12px;
		line-height: 1.58;
		margin: auto 0 0; }

	&__btn {
		border: 3px solid $gray;
		background: $mainBg;
		margin: auto 0 0;
		color: $themeColorD;

		&:after,
		&:before {
			display: none; }

		&:hover {
			background: transparent;
			border-color: $themeColorD; } } }
