
.s-prices {
  position: relative;
  .container {
    max-width: 1280px; } }

.prices-swiper {
  .swiper-slide {
    width: 384px;
    @include md {
      width: 360px; }
    @include sm {
      width: 302px; }
    @include xxxs {
      width: 280px; } }
  .swiper-container {
    @include laptop {
      overflow: visible; } } }

.prices-item {
  position: relative;
  clip-path: polygon(0 0, calc(100% - 25px) 0, 100% 25px, 100% 100%, 0 100%);

  &__title {
    @include bold;

    em {
      font-style: normal;
      color: #FE5D02; } }

  &__price {
    @include bold; } }
