.sponsors {
  position: relative;
  padding: 100px 0;
  @include md {
    padding: 40px 0; }
  .container {
    max-width: 1110px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      height: 1px;
      background: linear-gradient(270deg, rgba(187, 255, 0, 0) 0%, #BBFF00 50%, rgba(187, 255, 0, 0) 100%);
      opacity: 0.3; } }

  .section__title {
    @include bold;
    font-size: clamp(32px, 4vw, 64px);
    margin-bottom: clamp(24px, 3vw, 38px); }
  &__description {
    display: flex;
    flex-direction: column;
    text-align: center;
    max-width: 960px;
    margin: 0 auto 64px;
    gap: 64px;

    @include sm {
      font-size: 14px;
      gap: 32px;
      margin: 0 auto 32px; } }

  &__intro {
    @include medium;
    color: $gray;
    font-size: 20px;
    line-height: 1.36;
    text-align: center;

    @include sm {
      font-size: 16px; } }

  &__offer {
    @include bold;
    font-size: 24px;
    color: $themeColor;

    @include sm {
      font-size: 18px; }

    a {
      color: $themeColor; } } }

.sponsors-block {
  position: relative;
  padding: 18px 28px;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0);
    background: $themeColor;
    opacity: 0.3;
    z-index: -1; }

  &:after {
    content: '';
    position: absolute;
    left: 1px;
    top: 1px;
    right: 1px;
    bottom: 1px;
    clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0);
    background: $mainBg;
    z-index: 0; }

  & + & {
    margin-top: 24px; }

  &__title {
    @include medium;
    font-size: 14px;
    position: relative;
    z-index: 1;
    @include light;
    color: $themeColor;
    margin-bottom: 20px;
    @include sm {
      margin-bottom: 32px; } }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -20px;
    position: relative;
    z-index: 1;
    @include sm {
      margin: 0 -10px; } }

  &__item {
    text-align: center;
    width: 100%;

    @include tablet {
      display: flex;
      justify-content: center; }

    @include sm {
      max-width: 230px; }

    & > a {
      display: flex;
      justify-content: center;
      align-items: center;
      transition: opacity 0.3s;
      padding: 20px;
      @include sm {
        padding: 10px; }

      &:hover {
        opacity: 0.7; }

      picture {
        display: block; }

      img {
        display: block;
        max-width: 100%;
        transition: all ease 0.2s;
        transform: translateY(0);
        opacity: 1;
        height: auto; } }

    &--w-info {
      width: 100% !important;
      max-width: 100% !important;
      display: flex;
      flex-direction: column;
      align-items: center;
      order: 1; } }
  &__content {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    font-size: 12px;
    line-height: 1.45;
    @include sm {
      font-size: 10px; }
    a {
      color: inherit;
      text-decoration: underline;
      &:hover {
        text-decoration: none; } }
    p:not(:last-child) {
      margin-bottom: 10px; } }
  &__subtitle {
    font-weight: 600;
    margin-bottom: 5px; }
  &.Platinum {
    .sponsors-block__item {
      width: 50%;
      max-width: 50%;
      @include sm {
        width: 100%;
        max-width: 100%; } } }

  &.Gold {
    .sponsors-block__item {
      width: 33.33%;
      max-width: 33.3%;
      @include xs {
        width: 50%;
        max-width: 50%; } } }

  &.Silver {
    .sponsors-block__item {
      width: 25%;
      max-width: 25%;

      @include xs {
        width: 33.3%;
        max-width: 33.3%; } } }

  &.MediaPartner,
  &.Partner,
  &.TechPartner,
  &.EntertainmentPartner {

    .sponsors-block__item {
      width: 20%;
      max-width: 20%;

      @include xs {
        width: 25%;
        max-width: 25%; } } }

  &_auto {
    .sponsors-block__list {
      justify-content: space-between; }

    .sponsors-block__item {
      width: auto;

      @include sm {
        width: 25%; }
      @include xs {
        width: 33.3%; }

      img {
        max-width: 100%; }

      &:last-child {
        margin: 0 auto 0 0; } } } }
