.articles {
  padding-top: 142px;
  color: $white;
  @include md {
    padding-top: 40px; }
  &__title {
    font-size: 33px;
    line-height: 1.39;
    text-align: center;
    max-width: 627px;
    margin: 0 auto 85px;
    @include md {
      margin-bottom: 20px; } }
  &__subtitle {
    text-align: center;
    font-size: 22px;
    max-width: 627px;
    margin: 0 auto; }
  &__content {
    padding: 0 40px;
    @include md {
      padding: 0 20px; } } }

.article {
  padding: 100px 0;
  @include md {
    padding: 30px 0; }
  &:not(:last-child) {
    border-bottom: 1px solid #202421; }
  &__inner {
    display: flex;
    justify-content: space-between;
    max-width: 1274px;
    margin: 0 auto;
    @include md {
      flex-direction: column; } }
  &__img {
    width: 47%;
    margin-right: 60px;
    flex-shrink: 0;
    padding-top: 5px;
    @include md {
      margin-right: 0;
      width: 100%;
      margin-bottom: 20px;
      padding-top: 0; }
    img {
      width: 100%;
      @include md {
        width: auto;
        max-width: 100%;
        display: block;
        margin: 0 auto; } } }
  &__content {
    flex: 1; }
  &__title {
    font-size: 26px;
    color: $white;
    @include bold;
    margin-bottom: 35px;
    @include md {
      margin-bottom: 15px; } }
  &__description {
    color: #888;
    font-size: 15px;
    line-height: 2;
    margin-bottom: 43px;
    letter-spacing: 1.1px;
    @include md {
      margin-bottom: 20px; }
    @include sm {
      color: #aaaaaa; }

    ul {
      list-style: disc; }

    a {
      color: #fff; }

    p {
      &:not(:last-child) {
        margin-bottom: 23px;
        @include md {
          margin-bottom: 10px; } } } }
  &__link {
    text-decoration: none;
    color: #00ff00;
    font-size: 14px;
    text-transform: uppercase;
    &:hover {
      text-decoration: underline; } } }

.styled-text {
  margin: 32px 0 0;

  p {
    margin: 34px 0;
    font-size: clamp(14px, 2vw, 18px);
    line-height: 1.88; }

  h3 {
    position: relative;
    color: $white;
    padding: 0 0 0 clamp(50px, 5vw, 60px);
    font-size: clamp(18px, 3vw, 28px);
    margin: clamp(50px, 8vw, 100px) 0 clamp(26px, 8vw, 52px);
    font-weight: 700;
    line-height: 1.22;

    &:before {
      content: '';
      width: clamp(30px, 4vw, 40px);
      height: clamp(30px, 4vw, 40px);
      margin: 0 clamp(10px, 4vw, 20px) 0 0;
      position: absolute;
      left: 0;
      top: -4px;
      background: url(../img/video-rooms_bg.svg) center center;
      background-size: cover; } }

  ul {
    color: $white;
    font-size: clamp(14px, 3vw, 16px);
    font-weight: 300;
    margin: 18px 0 36px;
    line-height: 1.88;
    list-style: none;
    gap: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(340px,1fr));
    flex-wrap: wrap;

    @include sm {
      gap: 15px;
      grid-template-columns: repeat(auto-fit,minmax(240px,1fr)); }

    li {
      padding-left: 22px;
      position: relative;
      background: #FFFFFF10;
      padding: 34px 40px;
      border-radius: 20px;
      display: flex;
      align-items: center;

      @include sm {
        padding: 24px 30px; }

      &:after {
        content: '';
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $themeColor;

        @include sm {
          left: 12px; } } } } }
