.location {
	color: $white;
	padding: 160px 0 60px;

	@include tablet {
		padding-top: 120px; }

	@include md {
		padding-top: 100px; }

	@include sm {
		padding-top: 60px; }

	@include xs {
		padding-top: 40px; }

	.container {
		max-width: 1190px; }

	&__top {
		display: flex;
		position: relative;
		gap: 40px 126px;

		@include tablet {
			gap: 40px 80px; }

		@include sm {
			flex-direction: column; } }

	&__info {
		position: relative;
		z-index: 1;
		flex: 1 1; }

	&__map {
		position: relative;
		flex: 1 1;
		margin-top: -20px;

		@include tablet {
			width: 600px; }

		@include md {
			width: auto;
			max-width: 420px;
			margin: 0 auto; }

		@include sm {
			margin: 0 auto; }

		@include xxs {
			margin: 0 -20px; }

		.map {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			@include sm {
				width: auto;
				position: relative; }

			img {
				width: 100%;
				display: block; } } }

	&__title {
		line-height: 1;

		h3 {
			@include black;
			color: $themeColor;
			font-weight: 900;
			font-size: clamp(36px, 4vw, 64px);
			margin-bottom: 24px; }

		p {
			font-weight: 500;
			font-size: clamp(14px, 2vw, 18px);
			line-height: 1.8;
			color: $gray; } }

	&__address {
		line-height: 1.5;
		margin: 30px 0 0;

		h3 {
			@include bold;
			font-size: clamp(26px, 2vw, 32px);
			font-stretch: 120%;
			line-height: 1.24; }

		p {
			font-size: 14px;
			@include medium; } }

	&__video {
		width: 100%;
		display: block;
		margin: 100px 0 0;
		z-index: 1;
		position: relative;
		overflow: hidden;

		.video {
			border-radius: 0; }

		@include sm {
			margin: 20px 0 0;
			border-radius: 0;

			.video {
				border-radius: 0; } }

		@include xs {
			border-radius: 0;

			.video {
				border-radius: 0; } }


		.video {
			aspect-ratio: 1.68; } }

	&__slider {
		width: 100%;
		display: block;
		margin: 100px 0 0;
		z-index: 1;
		position: relative;

		@include sm {
			width: auto;
			margin: 1px -20px 0; } } }

.location-slider {
	position: relative;
	width: 100%;
	list-style: none;

	&__item {
		position: relative;
		display: flex;
		padding: 0;
		box-sizing: border-box;
		height: auto;
		aspect-ratio: 1.68; }

	&__descr {}

	&__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	.js-slider-prev,
	.js-slider-next {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 64px;
		height: 64px;
		background: $black;
		position: absolute;
		left: -70px;
		top: 50%;
		transform: translateY(-50%);
		z-index: 2;
		opacity: 1;
		cursor: pointer;
		transition: all ease 0.1s;

		@include laptop {
			width: 46px;
			height: 46px;
			left: 0px; }

		&.js-slider-next {
			left: auto;
			right: -70px;

			@include laptop {
				right: 0px; } }

		&.swiper-button-disabled {
			opacity: 0;
			visibility: hidden; }

		.icon {
			width: 12px;
			height: 24px;
			fill: $themeColor; } } }
