.header {
	z-index: 9;
	background-color: $themeColor;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	color: $black;
	@include md {
		bottom: auto;
		top: 0;
		padding: 0; }


	&__logo {
		display: none; }

	&__nav {
		@include md {
			width: 100%;
			margin: auto 0;
			padding: 20px 0; } }
	&__btn {
		position: absolute;
		top: 16px;
		right: 0;
		width: 214px;
		.btn {
			width: 100%;
			min-height: 48px;
			font-size: 14px;
			border-radius: 0;
			padding: 0;
			background: $black;
			color: $themeColor;

			&:hover, &:focus-visible {

				background: $black;
				color: $themeColor2; } }


		@include tablet {
			width: 160px; }
		@include md {
			width: auto;
			position: relative;
			margin: 0 0 20px;
			display: none; } }

	&__alt-btn {
		display: none;
		position: fixed;
		bottom: 0;
		right: 0;
		width: 214px;
		z-index: 2;
		pointer-events: auto;

		.btn {
			width: 100%;
			height: 48px;
			border-radius: 0;
			padding: 0;
			font-size: 14px;
			@extend .btn--border;
			@extend .btn--small; }

		@include md {
			width: auto;
			margin: 0;
			display: block;
			width: 160px; } }

	&__socials {
		position: absolute;
		display: flex;
		align-items: center;
		left: 15px;
		height: 50px;
		top: 16px;
		@include md {
			order: 3;
			margin-top: 0;
			margin-bottom: 15px;
			position: relative;
			left: 0;
			top: 0; }

		.social__link {
			&:hover, &:focus-visible {
				background: #fff;
				.icon {
					fill: #000  !important; } }

			@include tablet {
				width: 30px;
				height: 30px;
				clip-path: polygon(calc(100% - 7px) 0, 100% 7px, 100% 100%, 7px 100%, 0 calc(100% - 7px), 0 0);
				.icon {

					width: 14px;
					height: 14px;
					&-tw, &-fb, &-yb {
						width: 18px;
						height: 18px; } } } } }


	&__inner {
		position: relative;
		display: flex;
		max-width: 1480px;
		padding: 16px 20px;
		align-items: center;
		justify-content: center;
		min-height: 80px;
		margin: 0 auto;
		@include laptop {
			padding-left: 40px; }
		@include tablet {
			padding-left: 20px;
			min-height: 60px; }
		@include md {
			display: none;
			position: fixed;
			top: 0;
			left: 0;
			height: 100vh;
			height: calc(var(--vh, 1vh) * 100);
			height: 100%;
			overflow: hidden;
			overflow-y: auto;
			padding: 1px 0;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 100%;
			background-color: rgba(0,0,0,0.5);
			//transition: opacity 0.3s
			z-index: 3;
			&.is-open {
				display: flex;
				padding-left: 0; } } }


	&__burger {
		display: none;
		position: absolute;
		top: 10px;
		right: 10px;
		z-index: 3;
		@include md {
			display: inline-block;
			pointer-events: auto; } } }

.nav {
	display: flex;
	@include md {
		display: block; }

	&__link {
		text-decoration: none;
		color: $black;
		transition: 0.1s;
		font-size: 14px;
		@include semibold;
		height: 50px;
		display: flex;
		padding: 0 10px;
		align-items: center;
		justify-content: center;
		clip-path: polygon(calc(100% - 10px) 0, 100% 10px, 100% 100%, 10px 100%, 0 calc(100% - 10px), 0 0);
		@include tablet {
			padding: 0 8px; }
		@include md {
			color: $white; }


		&:hover, &:focus-visible {
			color: $black;
			background: rgba(0, 0, 0, 0.1);

			@include md {
				background: rgba(0, 0, 0, 0.5); } }

		@include md {
			font-size: 14px;
			display: flex;
			text-align: center;
			padding: 10px 20px;
			font-weight: 500;
			&:hover {
				background: none;
				color: #fff; } }
		@include sm {
			font-size: 12px; }
		.no-touch &:hover {
			color: $black;

			@include md {
				color: $white; } } } }


.burger {
	width: 60px;
	height: 60px;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	@include sm {
		width: 50px;
		height: 50px; }

	span {
		transition: 0.3s;
		display: block;
		height: 4px;
		width: 36px;
		background-color: #ffffff;
		border-radius: 5px;
		&:not(:last-child) {
			margin-bottom: 6px; } }
	&.is-active {
		span {
			&:nth-child(1) {
				transform: translate(0, 10px) rotate(45deg); }
			&:nth-child(2) {
				transform: translateX(0);
				opacity: 0; }
			&:nth-child(3) {
				transform: translate(0, -10px) rotate(-45deg); } } } }


