.s-party {
  position: relative;
  padding: clamp(50px, 5vw, 100px) 0 500px;

  @include md {
    padding: clamp(50px, 5vw, 100px) 0 300px; }


  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 100px;
    right: 0;
    bottom: 0;
    background: $themeColor;
    clip-path: polygon(100% 0%, 100% calc(0% + 200px), calc(0% + 476px) calc(100% - 50px), 0% calc(100% - 50px), 0 calc(100% - 170px), calc(0% + 420px) calc(100% - 170px));
    opacity: 0.4;
    z-index: 0; }

  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: 100px;
    right: 0;
    bottom: 0;
    background: $mainBg;
    clip-path: polygon(calc(100% + 1px) calc(0% + 1px), calc(100% - 1px) calc(0% + 199px), calc(0% + 475px) calc(100% - 51px), calc(0% - 1px) calc(100% - 51px), calc(0% - 1px) calc(100% - 169px), calc(0% + 421px) calc(100% - 169px));
    z-index: 0; }

  .container {
    max-width: 1200px;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      height: 1px;
      background: linear-gradient(270deg, rgba(187, 255, 0, 0) 0%, #BBFF00 50%, rgba(187, 255, 0, 0) 100%);
      opacity: 0.3; } }

  .section {
    &__title {
      @include bold;
      font-size: clamp(32px, 4vw, 64px);
      margin-bottom: clamp(36px, 6.4vw, 100px); }

    &__subtitle {
      margin-bottom: 38px;
      line-height: 1.36;
      max-width: 880px;
      color: $gray;
      @include sm {
        margin-bottom: 32px; } } } }

.party {

  &:after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 60px;
    width: 454px;
    height: 524px;
    background: url(../img/party.png) center center no-repeat;
    background-size: contain;
    z-index: 1;

    @include md {
      width: 228px;
      height: 262px;
      bottom: 20px; } }

  &__item {
    gap: 100px;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;

    @include sm {
      gap: 0;
      flex-direction: column-reverse; }

    &:nth-child(even) {
      flex-direction: row-reverse;

      @include sm {
        flex-direction: column-reverse; } }

    &:not(:first-child) {
      margin-top: 100px;

      @include sm {
        margin-top: 40px; } }

    @include md {
      gap: 50px; }

    @include sm {
      gap: clamp(20px, 4vw, 30px); } }

  &__text {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    max-width: 630px;
    min-width: 320px;
    padding: 0;
    justify-content: center;

    @include md {
      padding: 0; }

    @include sm {
      text-align: center;
      min-width: 0; } }


  &__images {
    flex: 1 1;
    max-width: 554px;
    min-width: 320px;
    gap: 24px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @include sm {
      margin: 0 auto;
      max-width: 380px;
      min-width: 0;
      gap: 5px; } }

  &__img {

    &.single {
      grid-area: 1 / 1 / 3 / 3; }

    &.fill-height {
      grid-area: 1 / 1 / 3 / 2; }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;

      @include sm {
        border-radius: 6px; } } }

  &__title {
    @include bold;
    text-transform: uppercase;
    font-size: clamp(24px, 4vw, 32px);
    line-height: 1.18; }

  &__desc {
    font-size: clamp(14px, 2.6vw, 20px);
    line-height: 1.5;
    font-weight: 500;
    margin: 18px 0 0;
    color: $gray; } }
